<template>
<section id="wedding">
  <div class="section-background"></div>
  <div class="section-content">
    <div id="title">
      <h2>The Wedding</h2>
    </div>
    <div id="info-container">
      <div class="info">
        <h3>Where</h3>
        <p>Rice Bowl Restaurant</p>
        <p>2378 Florin Road</p>
        <p>Sacramento, CA 95822</p>
      </div>
      <div class="info">
        <h3>When</h3>
        <p>5:30 pm</p>
        <p>July 7th, 2018</p>
      </div>
    </div>
  </div>
</section>
</template>

<script>
export default {
  name: 'TheWedding',
  data () {
    return {
      msg: 'What'
    }
  }
}
</script>

<style scoped>
#info-container {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-content: center;
}

.info {
  flex: 1;
}

.info h3 {
  font-family: 'Salmela';
}

@media (min-width: 500px) {
  #wedding {
    background: none;
  }

  .section-background {
    background-image: url('../../assets/images/couple_1_crop.jpg');
  }
  .section-content {
    background: transparent;
  }
}
</style>



