<template>
<section id="story">
  <div class="section-background"></div>
  <div class="section-content">
    <div id="title">
      <h2>Their Story</h2>
    </div>
    <div class="info">
      <p>The official journey towards "The Knowles" all started at Rosemont High almost 9 years ago. Looking at the pair from the outside, they were incredibly different. From one being captain of the swim team and the other not even being able to float in water, they didn't seem the most compatible. However, the two prove to be more than compatible in the way that they're both able overcome trials together and love one another through them. The love that Chi and Michael share is unique and is shown in the way they love others. This marriage is simply a symbol for the commitment and love that they already hold for each other.</p>
    </div>
  </div>
</section>
</template>

<script>
export default {
  name: 'TheStory',
  data () {
    return {
      msg: 'What'
    }
  }
}
</script>

<style scoped>
.info {
  padding: 0 1.5em;
  font-family: 'Courgette';
  font-size: 1em;
  line-height: 2em;
}

@media (min-width: 500px) {
  #story {
    background: none;
  }

  .section-background {
    background-image: url('../../assets/images/couple_2_crop.jpg');
  }
  .section-content {
    background: transparent;
  }
}
</style>



