<template>
<section id="rsvp">
  <div class="section-background"></div>
  <div class="section-content">
    <div id="title">
      <h2>RSVP</h2>
    </div>
    <div id="form-container">
      <p></p>
      <form action="https://formspree.io/michaelknowles.rhs@gmail.com" method="POST">
      <input type="hidden" name="_subject" value="Wedding RSVP" />
      <input type="hidden" name="_cc" value="chihuynh.rhs@gmail.com" />
      <input type="text" name="_gotcha" style="display:none" />
      <div class="form-field">
        <label>Let us know if you'll be coming.</label>
        <input type="radio"
          name="attending"
          v-model.trim="attending"
          value="yes"
          required>Accept</input>
        <input type="radio"
          name="attending"
          v-model.trim="attending"
          value="no"
          required>Decline</input>
      </div>
      <div class="form-field">
        <label>Your full name</label>
        <input type="text"
          name="fullName"
          v-model.trim="fullName"
          placeholder="Full Name"
          v-validate="'required|alpha_spaces'"
          required></input>
      </div>
      <div class="form-field" v-if="this.attending == 'yes'">
        <label>Number of people</label>
        <input type="number"
          name="people"
          placeholder="1"
          v-model.number="people"
          min="1"
          v-validate="'required|numeric'"></input>
      </div>
      <div class="form-field" v-if="this.attending == 'yes'">
        <label>Your phone number</label>
        <input type="tel"
          name="phone"
          required></input>
      </div>
      <div class="form-field">
        <label>Comment/Request</label>
        <input type="text"
          name="comment"
          placeholder="Comment"
          v-model="comment"></input>
      </div>
      <div class="form-field">
        <button type="submit" value="Send">Submit</button>
      </div>
      </form>
    </div><!-- #form-container -->
    <div id="display">
      <span v-if="fullName">{{ displayRsvp }}</span>
    </div>
  </div>
</section>
</template>

<script>
export default {
  name: 'TheRsvp',
  data () {
    return {
      msg: 'What',
      fullName: '',
      people: 1,
      comment: '',
      attending: 'unknown'
    }
  },
  computed: {
    displayRsvp () {
      if (this.$validator.errors.has('fullName')) {
        return 'Please enter a valid name.'
      }
      if (this.attending === 'yes') {
        if (this.people === 2) {
          return this.fullName + ' will be attending with 1 guest.'
        } else if (this.people > 2) {
          return this.fullName + ' will be attending with ' + (this.people - 1) + ' guests.'
        } else {
          return this.fullName + ' will be attending.'
        }
      } else if (this.attending === 'no') {
        return this.fullName + ' respectfully declines.'
      }
    }
  }
}
</script>

<style scoped>
#form-container {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-content: center;
  line-height: 2em;
}

.form-field {
  text-align: center;
  margin: .5em 0;
}

.form-field label {
  display: block;
}

#display {
  flex: 1;
}

@media (min-width: 500px) {
  #rsvp {
    background: none;
  }

  .section-background {
    background-image: url('../../assets/images/couple_dog_crop.jpg');
  }
  .section-content {
    background: transparent;
  }
}
</style>



